
import { PropType, toRefs } from 'vue'

export default {
  name: 'SvgIcon',
  props: {
    iconName: {
      type: String as PropType<String>,
      required: true
    },
    width: {
      type: Number as PropType<Number>,
      required: true
    },
    height: {
      type: Number as PropType<Number>,
      required: true
    },
    fillColor: {
      type: String as PropType<String>,
      default: '#BFCBD9'
    }
  },
  setup(props: any) {
    const { iconName: _iconName, width, height, fillColor } = toRefs(props)
    const iconName = '#' + _iconName.value
    
    return {
      iconName,
      width,
      height,
      fillColor
    }
  }
}
